:root {
  --font-inter: "Inter", sans-serif;
}

:root {
  --LimeGreen: hsl(163, 72%, 41%);
  --BrightRed: hsl(356, 69%, 56%);
  --Facebook: hsl(208, 92%, 53%);
  --Twitter: hsl(203, 89%, 53%);
  --InstagramStart: hsl(37, 97%, 70%);
  --InstagramEnd: hsl(329, 70%, 58%);
  --YouTube: hsl(348, 97%, 39%);
  --BgToggleStart: hsl(210, 78%, 56%);
  --BgToggleEnd: hsl(146, 68%, 55%);
  --BgToggle: hsl(230, 22%, 74%);
  --BgDark: hsl(230, 17%, 14%);
  --BgDarkTop: hsl(232, 19%, 15%);
  --BgDarkCard: hsl(228, 28%, 20%);
  --LightBlue: hsl(228, 34%, 66%);
  --White: hsl(0, 0%, 100%);
  --BgWhite: hsl(0, 0%, 100%);
  --PaleBlue: hsl(225, 100%, 98%);
  --GrayBlue: hsl(228, 12%, 44%);
  --DarkBlue: hsl(230, 17%, 14%);
}

*,
*::before,
*::after {
  box-sizing: border-box;
}

html {
  font-size: 100%;
  box-sizing: border-box;
}

body {
  margin: 0;
  padding: 0;
  font-family: var(--font-inter);
  background: var(--BgDark);
  color: var(--White);
}

h1,
h2,
h3 {
  margin-top: 0;
  line-height: 1.1;
}

h1 {
  font-size: 1.5rem;
  margin-bottom: 0.1875rem;
}
@media (min-width: 71.875em) {
  h1 {
    font-size: 1.75rem;
  }
}

a,
a:visited,
a:active {
  text-decoration: none;
}

.container {
  margin: 0 auto 2.875rem;
  padding: 0 1.5625rem;
  max-width: 69.375rem;
}
@media (min-width: 71.875em) {
  .container {
    padding: 0;
  }
}

.header {
  display: flex;
  flex-wrap: wrap;
  margin-top: 2.25rem;
}
@media (min-width: 40em) {
  .header {
    display: flex;
    align-items: center;
    justify-content: space-between;
  }
}
.header__title {
  width: 100%;
}
@media (min-width: 40em) {
  .header__title {
    width: auto;
  }
}
.header__subtitle {
  font-size: 0.875rem;
  font-weight: 700;
  color: var(--LightBlue);
}

.toggle {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  border: none;
  gap: 0.3rem 0.75rem;
}
.toggle label {
  font-size: 0.875rem;
  align-self: center;
}
.toggle label[for=dark] {
  grid-column: 1/2;
  justify-self: end;
}
.toggle label[for=system] {
  grid-column: 1/4;
  grid-row: 2/3;
  justify-self: center;
}
.toggle label[for=light] {
  grid-column: 3/4;
}
.toggle__wrapper {
  position: relative;
  grid-column: 2/3;
  grid-row: 1/2;
  height: 1.5rem;
}
.toggle input[type=radio] {
  opacity: 0;
  margin: 0 -0.125rem;
  height: 1.25rem;
  width: 1.25rem;
}
.toggle input[type=radio]:focus ~ .toggle__button {
  border: 2px solid #fff;
}
.toggle__background {
  height: 100%;
  display: block;
  position: absolute;
  width: 100%;
  top: 0;
  border-radius: 0.75rem;
  overflow: hidden;
  background: linear-gradient(225deg, var(--BgToggleStart) 0%, var(--BgToggleEnd) 98.02%);
  pointer-events: none;
}
.toggle__button {
  position: absolute;
  top: 0.1875rem;
  left: 0.1875rem;
  right: 100%;
  width: 1rem;
  height: 1rem;
  border-radius: 50%;
  background-color: var(--BgDark);
  transition: all 0.15s ease-in-out;
}
.toggle #light:checked ~ .toggle__button {
  left: calc(100% - 21px);
  right: 3px;
}
.toggle #system:checked ~ .toggle__button {
  left: 50%;
  right: auto;
  transform: translate(-50%);
}

.cards {
  display: grid;
  grid-template-columns: 1fr;
  gap: 1.875rem;
}
@media (min-width: 40em) {
  .cards {
    gap: 1.875rem;
    grid-template-columns: repeat(2, 1fr);
  }
}
@media (min-width: 71.875em) {
  .cards {
    gap: 1.875rem;
    grid-template-columns: repeat(4, 1fr);
  }
}

.card {
  position: relative;
  overflow: hidden;
  text-align: center;
  padding: 1.5625rem;
  background-color: var(--BgDarkCard);
  color: var(--LightBlue);
  border-radius: 0.3125rem;
}
.card__icon {
  margin-right: 0.5rem;
}
.card__icon--facebook {
  border-top: 0.3125rem solid var(--Facebook);
}
.card__icon--twitter {
  border-top: 0.3125rem solid var(--Twitter);
}
.card__icon--instagram {
  padding-top: 1.875rem;
}
.card__icon--instagram::before {
  content: "";
  position: absolute;
  display: block;
  left: 0;
  top: 0;
  width: 100%;
  height: 0.3125rem;
  background: linear-gradient(225deg, var(--InstagramEnd), var(--InstagramStart));
}
.card__icon--youtube {
  border-top: 0.3125rem solid var(--YouTube);
}
.card__platform {
  display: flex;
  justify-content: center;
  align-items: center;
  margin-bottom: 1.75rem;
  margin-top: 0.3125rem;
  height: 1.25rem;
}
.card__username {
  font-size: 0.75rem;
  font-weight: 700;
}
.card__followers {
  margin-bottom: 1.5625rem;
}
.card__count {
  letter-spacing: -0.125rem;
  color: var(--White);
  line-height: 1;
  margin-bottom: 0.25rem;
}
.card__count--big {
  font-size: 3.5rem;
  font-weight: 700;
}
.card__label {
  color: var(--LightBlue);
  font-size: 0.75rem;
  letter-spacing: 0.3125rem;
  font-weight: 400;
  text-transform: uppercase;
}
.card__change {
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 0.75rem;
  font-weight: 700;
}
.card__change--up {
  color: var(--LimeGreen);
}
.card__change--down {
  color: var(--BrightRed);
}
.card__change img {
  margin-right: 0.25rem;
}